@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldmedium.woff2") format(woff2), url("../../fonts/oswaldregular.woff") format(woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldmedium.woff2") format(woff2), url("../../fonts/oswaldmedium.woff") format(woff);
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

page {
  height: 100%;
  background-color: #FFFFFF;
  font-family: "Oswald", sans-serif;
}